import React from "react"

class Nav extends React.Component {

    handleShow = () => {
        var x = document.getElementsByClassName("i")[0];
        var i = document.getElementsByClassName("i-2")[0];
        var nav = document.getElementsByClassName("nav-bar")[0];
        nav.style = "left:0;position: absolute; transition:3s"
        x.style = "transition:2s;display:none;"
        i.style = "display:block;transition:2s"
    }
    handleHide =()=>{
        var x = document.getElementsByClassName("i")[0];
        var i = document.getElementsByClassName("i-2")[0];
        var nav = document.getElementsByClassName("nav-bar")[0];
        nav.style = "left:-17rem;position: absolute; transition:3s"
        x.style = "transition:2s;display:block;"
        i.style = "display:none;transition:2s"



    }
    render() {



        return (
            <div>
                <div className="Cover-box">
                    <div className="setting">
                        <div className="pic-1"></div>
                        <div className="pic-2"></div>
                        <div className="pic-3"></div>
                        <div className="pic-4"></div>
                    </div>
                    <div className="logo"></div>

                    <div className="show-nav">
                        <div className="i"><i class="fas fa-bars" onClick={this.handleShow}></i></div>
                        <div className="i-2"><i class="fas fa-times"  onClick={this.handleHide}></i></div>


                    </div>
                </div>
                <div className="nav-bar">


                    <div className="pic"><img src="eletric.gif" alt=""/></div>
                    <div className="conent">
                    <div className="icons">
                        
                        
                            <i class="fas fa-house-user"></i>
                            <i class="fas fa-user" ></i>
                            <i class="fas fa-briefcase"></i>
                            <i class="fas fa-tasks"></i>
                            <i class="far fa-address-card"></i>
                        
                    </div>

            <div className="ul">
            <ul>
            <li>Home</li>
        <li> About</li>
            <li>Services</li>
            <li>Projects </li>
            <li>Contact</li>
         </ul>
         </div>
            
                    </div>
            <div className="footer"></div>

                </div>

            </div>


        )
    }

}
export default Nav;