import React, { useState } from "react"

function Cover() {

    const [buttonText, setText] = useState("Click Me")

    return (
        <div>
            Hello
            <button onClick={() => setText("Thanks For Click")}>{buttonText}</button>


        </div>
    )

}

export default Cover;