import React from "react";


class Front extends React.Component {
    handlesetting = () => {

        var setting = document.getElementsByClassName("setting-text")[0];
        setting.style = ("left:0;transition:3s")
        this.handleAnimationsettinghide()

    }

    handleHide = () => {
        var setting = document.getElementsByClassName("setting-text")[0];
        setting.style = ("left:-15rem;transition:3s")
    }



    handleAnimationsetting = () => {
        var animation = document.getElementsByClassName("animation")[0];
        animation.style = ("left:0;transition:3s")
        this.handleHide()
    }
    handleAnimationsettinghide = () => {
        var animation = document.getElementsByClassName("animation")[0];
        animation.style = ("left:-25;transition:3s")


    }
    //////////////////////////////////////            Images Functiion                 ///////////////////////////////////////


    handleAnimation = () => {
        var Shownav = document.getElementsByClassName("show-nav")[0];
        Shownav.style = ("background-image:url(./show.gif)")
    }

    handleAnimation_2 = () => {
        var Shownav = document.getElementsByClassName("show-nav")[0];
        Shownav.style = ("background-image:url(./show-2.gif)")
    }

    handleAnimation_3 = () => {
        var Shownav = document.getElementsByClassName("show-nav")[0];
        Shownav.style = ("background-image:url(./show-3.gif)")
    }
    handleAnimation_4 = () => {
        var Shownav = document.getElementsByClassName("show-nav")[0];
        Shownav.style = ("background-image:url(./show-4.gif)")
    }
    handleAnimation_5 = () => {
        var Shownav = document.getElementsByClassName("show-nav")[0];
        Shownav.style = ("background-image:url(./show-5.gif)")
    }


    render() {
        return (
            <div className="Front">
                <div className="setting-2"><i class="fas fa-cog" onClick={this.handlesetting}></i></div>

                <div className="setting-text"><i class="fas fa-times" onClick={this.handleHide}></i>
                    <div className="s1" onClick={this.handleAnimationsetting}>Animation Change</div>
                    <div className="s1">Color Change</div>
                    <div className="s1"></div>
                    <div className="s1"></div>

                </div>
                <div className="animation">
                    <div className="a1 ani" onClick={this.handleAnimation}><img src="./show.gif" alt="" /> </div>
                    <div className="a2 ani" onClick={this.handleAnimation_2}><img src="./show-2.gif" alt="" /></div>
                    <div className="a3 ani" onClick={this.handleAnimation_3}><img src="./show-3.gif" alt="" /></div>
                    <div className="a4 ani" onClick={this.handleAnimation_4}><img src="./show-4.gif" alt="" /></div>
                    <div className="a5 ani" onClick={this.handleAnimation_5}><img src="./show-5.gif" alt="" /></div>
                </div>




            </div>
        )
    }
}

export default Front;